.expandable {
	transition: all 0.3s ease-in-out;
	transform: scale(100%);
}
.contracted {
	height: 53px;
	width: 53px;
}
.expanded {
	height: 100px;
	width: 100px;
}

#services-list {
	/* width: 40vw; */
	transition: ease-in-out 3s;
}

.fade-text {
	/* visibility: hidden; */
	opacity: 0;
	/* transition: all 0.3s linear; */
	transition: all 0.3s linear;
}

.fade-in {
	visibility: visible;
	opacity: 1;
}

@media only screen and (max-width: 900px) {
	#services-list {
		padding: 10px;
		width: 100%;
	}
}
@media only screen and (min-width: 901px) {
	#services-list {
		padding: 50px;
	}
}
