.blog-list-item {
	display: block;
	height: 500px;
	width: 500px;
	max-width: calc(100vw - 25px);
}

.blog-list-thumbnail {
	height: 250px;
	width: auto;
}

@media only screen and (max-width: 400px) {
	.blog-list-thumbnail {
		height: auto;
		max-height: 250px;
		width: auto;
		max-width: 100%;
	}
}

@media only screen and (min-width: 400px) {
	.blog-list-item {
		max-width: 400px;
	}
}
