hr.styled:before {
	/* Not really supposed to work, but does */
	display: block;
	content: "";
	height: 30px;
	margin-top: -31px;
	border-style: groove;
	border-color: grey;
	border-width: 0 0 1px 0;
	border-radius: 20px;
}
hr.styled {
	overflow: visible; /* For IE */
	height: 30px;
	border-style: groove;
	border-color: grey;
	border-width: 1px 0 0 0;
	border-radius: 20px;
}
