.layout-wrapper {
	display: flex;
	width: 100%;
	max-width: 1400px;
	margin: 24px 12px;
	justify-content: center;
}
.col-thin {
	max-width: 600px;
}

.header-img {
	height: 750px;
	width: 100vw;
	background-position: 100;
	background-size: auto;
}

@media only screen and (max-width: 900px) {
	#blog-page-wrapper {
		padding: 12px;
	}
	.layout-wrapper {
		display: block;
		margin: 24px 12px;
	}
	.col-responsive {
		width: 100%;
		margin-bottom: 24px;
	}
	.gutter {
		width: 0%;
	}
	.reverse {
		display: flex;
		flex-direction: column-reverse;
	}
	.header-img {
		height: 550px;
	}
}
@media only screen and (min-width: 901px) {
	.gutter {
		width: 1%;
	}
	.layout-wrapper {
		display: flex;
	}
	.col-responsive {
		width: 50%;
	}
}
